




export default {
	name: 'uni-tag',
	props: {
		type: { //defaultprimarysuccesswarningdangerroyal
			type: String,
			default: 'default'
		},
		size: { // normal, small
			type: String,
			default: 'normal'
		},
		text: String, //
		disabled: { //
			type: [String, Boolean],
			defalut: false
		},
		inverted: { //
			type: [String, Boolean],
			defalut: false
		},
		circle: { //
			type: [String, Boolean],
			defalut: false
		},
		mark: { //
			type: [String, Boolean],
			defalut: false
		}
	},
	methods: {
		onClick() {
			if (this.disabled === true || this.disabled === 'true') {
				return;
			}
			this.$emit('click')
		}
	}
}
